.home {}

.banner {
	background-image: url("../img/banner.jpg");
	height: 360px;
	@include max-screen($desktop) {
		height: 300px; } }
.banner-text {
	margin: auto;
	h1 {
		font-size: 2.8em; }
	div {
		font-size: 1.4em; } }
