* {
	@include box;
	padding: 0;
	margin: 0;
	border-collapse: collapse;
	&:before,&:after {
		@include box; } }
html, body {
	//width: 100%
 }	//height: 100%
body {
	//font: 14px/1.3 'geo', sans-serif
