	// overflow-x: hidden
	background: $color-bg-sky;
	min-width: 320px;
	@include max-screen($smartphone-landscape) {
		font-size: 12px; } }

img {
	max-width: 100%;
	height: auto;
	border: 0;
	vertical-align: middle; }
ul,li {
	list-style: none; }
a, input, textarea {
	color: inherit; }
a {
